import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import {AgGridModule} from "ag-grid-angular/main";
import { NgDatepickerModule } from 'ng2-datepicker';
import { LinkComponent } from './components/link-component/link-component.component';
import { ShareholderLinkComponent } from './components/shareholder-link-component/shareholder-link-component.component';
import { FeedbackView } from './components/feedback-view-component/feedback-view-component.component';
import { AllFeedbacks } from './components/all-feadbacks-component/all-feedbacks-component.component';
import { ShareholderView } from './components/shareholder-view-component/shareholder-view-component.component';
import { AllShareholders } from './components/all-shareholders-component/all-shareholders-component.component';
import { Home } from './components/home-component/home-component.component';
import { AppRoutingModule } from './app-routing.module';
import { HomeService } from './services/home.service';
import { ShareService } from './services/share.service';
import { BaseHttpGetService } from './services/base-http-get.service';
import { HttpModule } from '@angular/http';
import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';


import { AppComponent } from './app.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';


@NgModule({
  declarations: [
    AppComponent,
      LinkComponent,
      ShareholderLinkComponent,
    AllFeedbacks,
    FeedbackView,
    ShareholderView,
    AllShareholders,
    Home,

  ],
  imports: [
    BrowserModule,
    NgDatepickerModule,
    AppRoutingModule,
    FormsModule,
    HttpModule,
            AgGridModule.withComponents([
                LinkComponent,
                ShareholderLinkComponent
        ])
  ],
  providers: [
    HomeService,
    ShareService,
    BaseHttpGetService,
    DatePipe
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



