import { Injectable } from '@angular/core';
import { Headers, Http, Response, RequestOptions, URLSearchParams } from '@angular/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';

import { BaseHttpGetService } from './base-http-get.service';


import {
    Result,
    ResultT,
    FeedbackForm,
    FeedbackFormData
} from '../../view-models/view-models';

@Injectable()
export class HomeService extends BaseHttpGetService {
    //private formsUrl = 'http://localhost:58599/FeedbackService.svc/Feedback';  // URL to web API
    private formsUrl = 'api/feedback';

    constructor(private http: Http) { super(); }

    public getAllForms(){
        return this.http.get(`${this.formsUrl}/GetAll`)
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    }

    public getForm(id: number) {
        return this.http.get(`${this.formsUrl}/GetData/` + id)
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    }
    public getPeriodForm(minDate: string, maxDate: string, feedbackFormId:string, search:string ) {
        return this.http.get(`${this.formsUrl}/GetByFilter/` + minDate + '/' + maxDate + '/' + feedbackFormId + '/'+ search )
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    }
    public getPeriodScv(minDate: string, maxDate: string, feedbackFormId:string, search:string ) {
        let url = `${this.formsUrl}/Export/` + minDate + '/' + maxDate + '/' + feedbackFormId + '/' + search;
        window.open(url);
    }
    public getScv(id: number) {
        let url = `${this.formsUrl}/ExportSingle/` + id;
        window.open(url);
    }
}


