import { Component, OnInit } from '@angular/core';
import {Location} from '@angular/common';

import {
  FeedbackFormData,
  FeedbackForm,
  ShareholderAccount
} from '../../../view-models/view-models';
import { HomeService } from '../../services/home.service';
import 'rxjs/add/operator/switchMap';
import { Subscription } from 'rxjs';
import { ActivatedRoute, Router, Params } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './feedback-view.component.html',
  styleUrls: ['./feedback-view.componen.css'],
})
export class FeedbackView implements OnInit {
    public id: any;
    private idSubscription: Subscription;
    constructor(private _location: Location, private HomeService: HomeService, private route: ActivatedRoute,
        private router: Router,) {  
    }
    formId: number;
    public form = {} ;
    formData: ShareholderAccount;
    single_object: FeedbackForm;
    backClicked() {
        this._location.back();
    }
    export() {
      this.idSubscription = this.route.params.subscribe((params: any) => {
        this.id = params.id;
        this.HomeService.getScv(+this.id)
      })
    }

    ngOnInit(): void {
        this.idSubscription = this.route.params.subscribe((params: any) => {
          this.id = params.id;
          this.HomeService.getForm(+this.id).then(response => {
            this.formData = response;

            return this.formData
        })  
    });
    
  }   
//   save(): void {
//     this.HomeService.saveForm(this.formData)
//         .then(result => {
//               console.log("Saved Form");
//         })
// }
  }



