import { Injectable } from '@angular/core';
import { Headers, Http, Response, RequestOptions, URLSearchParams } from '@angular/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';

import { BaseHttpGetService } from './base-http-get.service';


import {
    Result,
    ResultT,
    FeedbackForm,
    FeedbackFormData,
    ShareholderAccount
} from '../../view-models/view-models';

@Injectable()
export class ShareService extends BaseHttpGetService {
    //private formsUrl = 'http://localhost:58599/ShareholderAccountService.svc';  // URL to web API
    private formsUrl = 'api/shareholder';  

    constructor(private http: Http) { super(); }

    public getAllForms(){
        return this.http.get(`${this.formsUrl}/GetFiltered/null`)
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    }

    public getForm(id: number) {
        return this.http.get(`${this.formsUrl}/` + id)
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    }
    public getFilter(search: string) {
        return this.http.get(`${this.formsUrl}/GetFiltered/` + search)
            .toPromise()
            .then(response => {
                var result = response.json();
                return result;
            });
    } 


    // public saveForm(formData: ShareholderAccount): Promise<Result> {
    //     var headers = new Headers();
    //     headers.append('Content-Type', 'application/json');
    //     return this.http.post(`http://localhost:58599/ShareholderAccountService.svc/Shareholder/Save`, formData)
    //         .toPromise()
    //         .then(response => response.json() as Result);
    // }

    public getPeriodScv(search:string ) {
        let url = `${this.formsUrl}/Export/` + search;
        window.open(url);
    }
    public getScv(id: number) {
        let url = `${this.formsUrl}/ExportSingle/` + id;
        window.open(url);
    }
}


