import { Component , OnInit} from '@angular/core';
import { GridOptions, IDatasource, IGetRowsParams, ColDef, ColGroupDef, NodeChildDetails, RowNode } from "ag-grid";
import { ShareholderLinkComponent } from '../shareholder-link-component/shareholder-link-component.component';
import { ViewChild, HostListener } from "@angular/core";
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router, Params, NavigationExtras } from '@angular/router';
import { HomeService } from '../../services/home.service';
import { ShareService } from '../../services/share.service';


import {
    ShareholderAccount,
    ResultCode
} from '../../../view-models/view-models';

@Component({
  selector: 'all-shareholders',
  templateUrl: './all-shareholders-component.component.html',
  styleUrls: ['../../app.component.css']
})

export class AllShareholders implements OnInit {
  title = 'app';
  public gridOptions: GridOptions;
  date: Date;
  public search:string = null;

    constructor(
        private ShareService: ShareService,
    ) {
        this.date = new Date();
    }

forms: ShareholderAccount[];
filter() {

}

clear(): void {
    this.search = null;
    // this.refresh();
}

export() {
    this.ShareService.getPeriodScv(this.search)
}

ngOnInit(): void {
 let ctrl = this;
        this.gridOptions = <GridOptions>{};
        this.gridOptions.columnDefs = [
            {
                headerName: "ID",
                field: "ShareholderAccountId",
                cellRendererFramework: ShareholderLinkComponent,
            },
            {
                headerName: "Имя",
                field: "LastName",
            },
            {
                headerName: "Отчество",
                field: "MiddleName",
            },
            {
                headerName: "Телефон",
                field: "Phone",
            },
            {
                headerName: "Регион",
                field: "Region",
            },
            {
                headerName: "Язык",
                field: "Language",
            },
            {
                headerName: "Компания",
                field: "CompanyName",
            },
            {
                headerName: "Город",
                field: "City",
            },
            {
                headerName: "Страна",
                field: "Country",
            },

        ];
        this.ShareService.getFilter(this.search).then(response => {
            this.forms = response;
            this.gridOptions.api.setRowData(this.forms);

            return this.forms
        });
}
refresh(){
    this.ShareService.getFilter(this.search).then(response => {
            this.forms = response;
            this.gridOptions.api.setRowData(this.forms);

            return this.forms
        });
}

}



