import { Component, OnInit } from '@angular/core';
import {Location} from '@angular/common';

import {
  FeedbackFormData,
  FeedbackForm,
} from '../../../view-models/view-models';
import { ShareService } from '../../services/share.service';
import 'rxjs/add/operator/switchMap';
import { Subscription } from 'rxjs';
import { ActivatedRoute, Router, Params } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './shareholder-view.component.html'
})
export class ShareholderView implements OnInit {
    public id: any;
    private idSubscription: Subscription;
    constructor(private _location: Location, private ShareService: ShareService, private route: ActivatedRoute,
        private router: Router,) {  
    }
    formId: number;
    public form = {} ;
    formList: FeedbackFormData;
    formData: FeedbackForm;
    single_object: FeedbackForm;
    backClicked() {
        this._location.back();
    }
    export() {
      this.idSubscription = this.route.params.subscribe((params: any) => {
        this.id = params.id;
        this.ShareService.getScv(+this.id)
      })
    }

    ngOnInit(): void {
        this.idSubscription = this.route.params.subscribe((params: any) => {
            this.id = params.id;
            this.ShareService.getForm(+this.id).then(response => {
                this.formData = response;

                return this.formData
            })
    });
    
  }   
  }



