import { Component , OnInit} from '@angular/core';
import { GridOptions, IDatasource, IGetRowsParams, ColDef, ColGroupDef, NodeChildDetails, RowNode } from "ag-grid";
import { DatepickerOptions } from 'ng2-datepicker';
import { LinkComponent } from '../link-component/link-component.component';
import { ViewChild, HostListener } from "@angular/core";
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router, Params, NavigationExtras } from '@angular/router';
import { HomeService } from '../../services/home.service';
import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

import {
    FeedbackForm,
    ResultCode
} from '../../../view-models/view-models';

@Component({
  selector: 'all-feedbacks',
  templateUrl: './all-feedbacks-component.component.html',
  styleUrls: ['../../app.component.css']
})

export class AllFeedbacks implements OnInit {
  title = 'app';
  public gridOptions: GridOptions;
  date: Date;
  public minDate: string;
  public maxDate: string; 
  public feedbackFormId: string = null;
  public search:string = null;

  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'MMM D[,] YYYY',
    barTitleFormat: 'MMMM YYYY',
    firstCalendarDay: 1 // 0 - Sunday, 1 - Monday
  };
    constructor(
        private HomeService: HomeService,
        private datePipe: DatePipe
    ) {
        this.date = new Date();
    }

forms: FeedbackForm[];
filter() {
    this.maxDate = this.datePipe.transform(this.maxDate, 'yyyy-MM-dd');
    this.minDate = this.datePipe.transform(this.minDate, 'yyyy-MM-dd');
    this.HomeService.getPeriodForm(this.minDate, this.maxDate, this.feedbackFormId, this.search).then(response => {
        this.forms = response;
        this.gridOptions.api.setRowData(this.forms);
        return this.forms
    })     
}

clear(): void {
    this.maxDate = '';
    this.minDate = '';
    this.feedbackFormId = null;
    this.search = null;
    this.refresh();
}

export() {
    this.maxDate = this.datePipe.transform(this.maxDate, 'yyyy-MM-dd');
    this.minDate = this.datePipe.transform(this.minDate, 'yyyy-MM-dd');
    this.HomeService.getPeriodScv(this.minDate, this.maxDate, this.feedbackFormId, this.search)
}

ngOnInit(): void {
 let ctrl = this;
        this.gridOptions = <GridOptions>{};
        this.gridOptions.columnDefs = [
            {
                headerName: "ID",
                field: "FormId",
            },
            {
                headerName: "Название формы обратной связи",
                field: "FormTitle",
                cellRendererFramework: LinkComponent,
                width: 400
            },
            {
                headerName: "Созданно",
                field: "CreatedDate",
            },
            {
                headerName: "Referer",
                field: "Referer",
            },
            {
                headerName: "Статус",
                field: "Status",
            },
            {
                headerName: "UserAgent",
                field: "UserAgent",
            },

        ];
        this.HomeService.getAllForms().then(response => {
            this.forms = response;
            this.gridOptions.api.setRowData(this.forms);

            return this.forms
        });
}
refresh(){
    this.HomeService.getAllForms()
        .then(response => {
                this.forms = response;
                this.gridOptions.api.setRowData(this.forms);
                this.minDate = null;
                this.maxDate= null; 
                return this.forms
            });
}

}



